@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
body{margin:0;font-family:'Merriweather', serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#7eaba6}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.background{background-color:#61dafb}

.around{display:block;padding:20px 60px;color:#7eaba6;height:100px}.around .menuWelcome{display:inline-block;float:left}.around .menuDiv{display:block;float:right}.around .menuDiv .menu{display:inline-block;float:right;list-style-type:none;margin:0 auto}.around .menuDiv .menu .menuStyle{text-decoration:none;color:#7eaba6}

.firstForm{display:block;padding:50px;height:300px}.firstForm .form{display:block}.firstForm .form input{padding:15px 35px;margin:10px;border:1px solid black}.firstForm .form .greenBtn{background-color:#7eaba6;padding:15px 100px;margin:10px;color:white}@media (min-width: 576px){.firstForm{display:flex;flex-wrap:wrap}}@media (min-width: 768px){.firstForm{display:flex;flex-wrap:wrap;background-image:url("/wheel-of-life--project/static/media/IMG-20190825-WA0003(1).1f221a0a.jpg");background-position:right bottom;background-size:500px 400px;background-repeat:no-repeat}}@media (min-width: 992px){.firstForm{display:flex;flex-wrap:wrap}}@media (min-width: 1200px){.firstForm{display:flex;flex-wrap:wrap}}

.wheel{padding:10px 20px}.wheel .nameState{text-align:center}.wheel .buttons{align-items:center}.wheel .numberButton{margin-left:20px;padding:10px 20px;background-color:#7eaba6;color:white;position:center}.wheel .renderedChart{margin-top:20px;padding:10px 20px;background-color:#7eaba6;color:white;position:center}

.nowWheel{text-align:center}button{background-color:#7eaba6;padding:15px 100px;margin:10px;color:white}

.frame{padding:15px}.frame ul{list-style-type:none;display:flex;flex-wrap:wrap;padding:10px;justify-content:space-between}.frame ul li{width:150px;height:150px;border:1px solid black;background-position:center;background-size:cover}@media (min-width: 768px){ul{justify-content:center}}

.futureText{text-align:center}

